import Eyeofcloud from '../../lib/eyeofcloud';
import {
  EventTags,
  EyeofcloudDecideOption,
  EyeofcloudDecision,
  EyeofcloudDecisionContext,
  EyeofcloudForcedDecision,
  UserAttributes,
} from '../../lib/shared_types';
import { CONTROL_ATTRIBUTES } from '../utils/enums';

export default class EyeofcloudUserContext {
  private eyeofcloud: Eyeofcloud;
  private userId: string;
  private attributes: UserAttributes;
  private forcedDecisionsMap: { [key: string]: { [key: string]: EyeofcloudForcedDecision } };

  constructor({
    eyeofcloud,
    userId,
    attributes,
  }: {
    eyeofcloud: Eyeofcloud,
    userId: string,
    attributes?: UserAttributes,
  }) {
    this.eyeofcloud = eyeofcloud;
    this.userId = userId;
    this.attributes = { ...attributes } ?? {};
    this.forcedDecisionsMap = {};
  }

  /**
   * Sets an attribute for a given key.
   * @param     {string}                     key         An attribute key
   * @param     {any}                        value       An attribute value
   */
  setAttribute(key: string, value: unknown): void {
    this.attributes[key] = value;
  }

  getUserId(): string {
    return this.userId;
  }

  getAttributes(): UserAttributes {
    return { ...this.attributes };
  }

  getEyeofcloud(): Eyeofcloud {
    return this.eyeofcloud;
  }

  /**
   * Returns a decision result for a given flag key and a user context, which contains all data required to deliver the flag.
   * If the SDK finds an error, it will return a decision with null for variationKey. The decision will include an error message in reasons.
   * @param     {string}                     key         A flag key for which a decision will be made.
   * @param     {EyeofcloudDecideOption}     options     An array of options for decision-making.
   * @return    {EyeofcloudDecision}                     A decision result.
   */
  decide(
    key: string,
    options: EyeofcloudDecideOption[] = []
  ): EyeofcloudDecision {

    return this.eyeofcloud.decide(this.cloneUserContext(), key, options);
  }

  /**
   * Returns an object of decision results for multiple flag keys and a user context.
   * If the SDK finds an error for a key, the response will include a decision for the key showing reasons for the error.
   * The SDK will always return key-mapped decisions. When it cannot process requests, it will return an empty map after logging the errors.
   * @param     {string[]}                   keys        An array of flag keys for which decisions will be made.
   * @param     {EyeofcloudDecideOption[]}   options     An array of options for decision-making.
   * @return    {[key: string]: EyeofcloudDecision}      An object of decision results mapped by flag keys.
   */
  decideForKeys(
    keys: string[],
    options: EyeofcloudDecideOption[] = [],
  ): { [key: string]: EyeofcloudDecision } {

    return this.eyeofcloud.decideForKeys(this.cloneUserContext(), keys, options);
  }

  /**
   * Returns an object of decision results for all active flag keys.
   * @param     {EyeofcloudDecideOption[]}   options     An array of options for decision-making.
   * @return    {[key: string]: EyeofcloudDecision}      An object of all decision results mapped by flag keys.
   */
  decideAll(
    options: EyeofcloudDecideOption[] = []
  ): { [key: string]: EyeofcloudDecision } {

    return this.eyeofcloud.decideAll(this.cloneUserContext(), options);
  }

  /**
   * Tracks an event.
   * @param     {string}                     eventName The event name.
   * @param     {EventTags}                  eventTags An optional map of event tag names to event tag values.
   */
  trackEvent(eventName: string, eventTags?: EventTags): void {
    this.eyeofcloud.track(eventName, this.userId, this.attributes, eventTags);
  }

  /**
   * Sets the forced decision for specified eyeofcloud decision context.
   * @param     {EyeofcloudDecisionContext}   context      EyeofcloudDecisionContext containing flagKey and optional ruleKey.
   * @param     {EyeofcloudForcedDecision}    decision     EyeofcloudForcedDecision containing forced variation key.
   * @return    {boolean}                     true if the forced decision has been set successfully.
   */
  setForcedDecision(context: EyeofcloudDecisionContext, decision: EyeofcloudForcedDecision): boolean {
    const flagKey = context.flagKey;

    const ruleKey = context.ruleKey ?? CONTROL_ATTRIBUTES.FORCED_DECISION_NULL_RULE_KEY;
    const variationKey  = decision.variationKey;
    const forcedDecision = { variationKey };

    if (!this.forcedDecisionsMap[flagKey]) {
      this.forcedDecisionsMap[flagKey] = {};
    }
    this.forcedDecisionsMap[flagKey][ruleKey] = forcedDecision;

    return true;
  }

  /**
   * Returns the forced decision for specified eyeofcloud decision context.
   * @param     {EyeofcloudDecisionContext}  context  EyeofcloudDecisionContext containing flagKey and optional ruleKey.
   * @return    {EyeofcloudForcedDecision|null}       EyeofcloudForcedDecision for specified context if exists or null.
   */
  getForcedDecision(context: EyeofcloudDecisionContext): EyeofcloudForcedDecision | null {
    return this.findForcedDecision(context);
  }

  /**
   * Removes the forced decision for specified eyeofcloud decision context.
   * @param     {EyeofcloudDecisionContext}  context  EyeofcloudDecisionContext containing flagKey and optional ruleKey.
   * @return    {boolean}                    true if the forced decision has been removed successfully
   */
  removeForcedDecision(context: EyeofcloudDecisionContext): boolean {
    const ruleKey = context.ruleKey ?? CONTROL_ATTRIBUTES.FORCED_DECISION_NULL_RULE_KEY;
    const flagKey = context.flagKey;

    let isForcedDecisionRemoved = false;

    if (this.forcedDecisionsMap.hasOwnProperty(flagKey)) {
      const forcedDecisionByRuleKey = this.forcedDecisionsMap[flagKey];
      if (forcedDecisionByRuleKey.hasOwnProperty(ruleKey)) {
        delete this.forcedDecisionsMap[flagKey][ruleKey];
        isForcedDecisionRemoved = true;
      }
      if (Object.keys(this.forcedDecisionsMap[flagKey]).length === 0) {
        delete this.forcedDecisionsMap[flagKey];
      }
    }

    return isForcedDecisionRemoved;
  }

  /**
   * Removes all forced decisions bound to this user context.
   * @return    {boolean}                    true if the forced decision has been removed successfully
   */
  removeAllForcedDecisions(): boolean {
    this.forcedDecisionsMap = {};
    return true;
  }

  /**
   * Finds a forced decision in forcedDecisionsMap for provided eyeofcloud decision context.
   * @param     {EyeofcloudDecisionContext}     context  EyeofcloudDecisionContext containing flagKey and optional ruleKey.
   * @return    {EyeofcloudForcedDecision|null}          EyeofcloudForcedDecision for specified context if exists or null.
   */
  private findForcedDecision(context: EyeofcloudDecisionContext): EyeofcloudForcedDecision | null {
    let variationKey;
    const validRuleKey = context.ruleKey ?? CONTROL_ATTRIBUTES.FORCED_DECISION_NULL_RULE_KEY;
    const flagKey = context.flagKey;

    if (this.forcedDecisionsMap.hasOwnProperty(context.flagKey)) {
      const forcedDecisionByRuleKey = this.forcedDecisionsMap[flagKey];
      if (forcedDecisionByRuleKey.hasOwnProperty(validRuleKey)) {
        variationKey = forcedDecisionByRuleKey[validRuleKey].variationKey;
        return { variationKey };
      }
    }

    return null;
  }

  private cloneUserContext(): EyeofcloudUserContext {
    const userContext = new EyeofcloudUserContext({
      eyeofcloud: this.getEyeofcloud(),
      userId: this.getUserId(),
      attributes: this.getAttributes(),
    });

    if (Object.keys(this.forcedDecisionsMap).length > 0) {
      userContext.forcedDecisionsMap = { ...this.forcedDecisionsMap };
    }

    return userContext;
  }
}
